<template>
  <v-dialog v-model="isOpen" persistent width="500px">
    <v-card>
      <v-card-title>{{selectedPoint.name}}</v-card-title>
      <v-card-text>
        <v-expansion-panels :value="[0, 1]" multiple accordion>
          <!-- Point Write -->
          <v-expansion-panel>
            <v-expansion-panel-header>
              <h3>Point Write</h3>
            </v-expansion-panel-header>
            <v-expansion-panel-content>
              <v-row>
                <v-col cols="12" sm="6" md="4">
                  <v-select v-model="user_options_point_value.priority" :items="priority_array" label="Priority"/>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                    <v-text-field v-model.number="user_options_point_value.point_write_value" label="Point Write Value" type="number"></v-text-field>
                  </v-col>
                <v-spacer></v-spacer>
                <v-btn @click="savePointValue(user_options_point_value, 'write')" color="primary" text style="margin: auto">Send&nbsp;<v-icon>mdi-send </v-icon> </v-btn>
              </v-row>
            </v-expansion-panel-content>
          </v-expansion-panel>
          <!-- Point Release Priority -->
          <v-expansion-panel>
            <v-expansion-panel-header>
              <h3>Point Release Priority</h3>
            </v-expansion-panel-header>
            <v-expansion-panel-content>
              <v-row>
                <v-col cols="12" sm="6" md="4">
                  <v-select v-model="user_options_point_value.priority" :items="priority_array" label="Priority"/>
                </v-col>
                <v-spacer></v-spacer>
                <v-btn @click="savePointValue(user_options_point_value, 'release')" color="primary" text style="margin: auto">send&nbsp;<v-icon>mdi-send </v-icon></v-btn>
              </v-row>
              <v-row>
                <v-col cols="12" sm="6" md="8">
                  <h4 class="text-start">Release All Priority</h4>
                </v-col>
                <v-spacer></v-spacer>
                <v-btn @click="savePointValue(user_options_point_value, 'release', true)" color="primary" text style="margin: auto">send&nbsp;<v-icon>mdi-send </v-icon></v-btn>
              </v-row>
            </v-expansion-panel-content>
          </v-expansion-panel>
          <!-- Point Release Priority -->
        </v-expansion-panels>
      </v-card-text>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn color="darken-1" text @click="onClose">Close</v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
import axios from '../../axios';
import * as modbusServerService from '../../services/modbusServerService';

export default {
  props: ['on-close', 'is-open', 'selected-point', 'api', 'response-from-dialog'],
  data: () => ({
    user_options_point_value: {},
    priority_array: ['_1','_2','_3','_4','_5','_6','_7','_8','_9','_10','_11','_12','_13','_14','_15','_16'],
  }),
  watch: {
    isOpen(val) {
      if(val) this.dataInit();
    },
  },
  methods: {
    dataInit(){
        this.user_options_point_value = { priority: '_16', point_write_value: 0};
    },
    getUrlApi(){
      switch(this.api){
        case 'modbus':
          return `${modbusServerService.endpoint}/point/`;
        case 'rubix':
          return 'point-server/generic/point/';
        case 'bac-net':
          return 'bac-net/point/value/';
      }
    },
    async savePointValue(data, endPointType, isReleaseAll) {
        const url = this.getUrlApi();
        const value = endPointType == "write" ? data.point_write_value : null;
        const key = `${[data.priority]}`;
        const priority_data = !isReleaseAll ? { [key] : value } : {"_1": null, "_2": null, "_3": null, "_4": null, "_5": null, "_6": null, "_7": null, "_8": null, "_9": null, "_10": null, "_11": null, "_12": null, "_13": null, "_14": null, "_15": null};
        const body = { "priority_array_write":  priority_data};
        const result =  await axios.patch(`${url}${this.selectedPoint.uuid}`, body);
        if(!result) return this.$toasted.show('fail!', { type: 'error' });
        this.responseFromDialog(result);
        return !isReleaseAll ? this.$toasted.show(`${data.priority}: ${value}`) : this.$toasted.show('Release All Priority Success');
    },
  },
};
</script>
